@import "../../_globalColor";

.card-image {
  //max-width: 100%;
  //height: auto;
  width: 250px;
}
.card-title {
  color: $textColor;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  margin: 15px 0 0 0;
}

.card-subtitle {
  color: $subTitle;
  font-size: 17px;
  line-height: 1.5rem;
  margin-top: 12px;
}
.project-subtitle{
  text-transform: uppercase;
}

.project-card-padding {
  padding: 2.5rem;
}

.project-card-dark {
  box-shadow: 0px 10px 30px -15px $darkBoxShadow;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;
}

.project-detail {
  text-align: center;
}
// .project-card-light:hover {
//   box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
// }
.project-card-dark:hover {
  box-shadow: 0px 20px 30px -10px $darkBoxShadow;
}
.project-image {
  //position: relative;
  height: 250px;
  //overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem 1rem;
}

.button-bottom { 
  // position: relative;
  // bottom: 30px;
  margin: 0;
}

.button-bottom .main-button:hover { 
  // position: relative;
  // bottom: 30px;
  background-color: white;
}

.project-card-footer {
  //margin-top: 40px;
  display: flex;
  justify-content: center;
  //margin-bottom: 30px;
  //position: absolute;
}

