@import "../../_globalColor";

.button-greeting-div {
  display: flex;
}

.greeting-text {
  font-size: 64px;
  line-height: 1;
  color: $textColor !important;
}

.greeting-text-p {
  font-size: 24px;
  line-height: 40px;
  font-family: "system-ui";
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

.button1-margin{
  margin: 20px;
  margin-left: 0;
}

.button2-margin{
  margin: 20px;
}


@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* Media Query */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .button-greeting-div {
    justify-content: space-around;
  }
  .greeting-text {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: 1.4;
    text-align: center;
  }
  .button1-margin{
    margin: 10px;
  }
  
  .button2-margin{
    margin: 10px;
  }
}
