@import "../../_globalColor";

.logo_wrapper {
  height: 100vh;
  width: 100vw;
  align-items: center;
  justify-content: center;
}

.screen {
  // height: 100%;
  // display: flex;
  // align-items: center;
  // justify-content: center;

  overflow: hidden;
  border-radius: 100%;
  animation: grow 5.8s forwards;
}

.lightbg{
  background-color: $textColor;// $signatureColor;//#1b4496;
}

@keyframes grow {
  0% {
    transform: scale(0);
  }
  10% {
    transform: scale(1);
    border-radius: 0%;
    height: 100%;
    width: 100%;
  }
  90% {
    transform: scale(1);
    border-radius: 0%;
    height: 100%;
    width: 100%;
  }
  100% {
    transform: scale(0);
    transform-origin: 50% 50%;
    border-radius: 100%;
  }
}

#logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: auto; 
}

@media (max-width: 768px) {
  #logo {
    width: 300px;
    margin-left: 5%;
  }
}
