@import "../../_globalColor";

.dark-mode a {
  color: $textColorDark;
}

.contact-detail-email {
  font-size: 24px;
  font-weight: 600;
  color: $subTitle;
  text-decoration: none;
}

.location {
  margin-top: 15px;
  a{
    text-decoration: none;
    color: $subTitle;
  }
}

.location:hover {
  a{
    color: $textColor;
  }
}

.contact-detail-email:hover {
  color: $textColor;
  text-shadow: 2px 1px 2px $contactDetailHoverTS;
  transition: all 0.3s;
}

.contact-subtitle {
  text-transform: uppercase;
}

.contact-margin-top {
  margin-top: 4rem;
}

/* Media Query */
@media (max-width: 768px) {

  .contact-subtitle {
    font-size: 16px;
    text-align: center;
  }

  .contact-detail-email {
    font-size: 20px;
  }

  .contact-left {
    text-align: center;
  }

  .location {
    a{
      font-size: 16px;
    }
  }
}