@import "../_globalColor";

.dark-mode {
  background-color: $darkBackground;
}

.light-mode {
  background-color: $lightBackground1;
}

.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 3rem;
}

@media (max-width: 320px) {
  .main {
    width: auto;
  }
}

.component-main {
  display: flex;
  gap: 20px;
}

.component-main > * {
  flex: 1;
  margin-bottom: 30px;
}

.component-image-div > * {
  max-width: 100%;
  height: auto;
}

.component-heading {
  font-size: 56px;
  font-weight: 400;
}

.subTitle {
  color: $subTitle;
}

@media (max-width: 1380px) {
  .component-heading {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .component-subtitle{
    font-size: 16px;
    text-align: center;
  }
  .component-heading {
    font-size: 30px;
    text-align: center;
  }
  .component-main {
    display: block;
  }
  .main {
    margin-top: 1rem;
  }
}

.component-card {
  //position: relative;
  background-color: $lightBackground2;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.component-card-dark {
  position: relative;
  background-color: $darkBackground;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
}

.component-card:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
}

.component-subtitle{
  text-transform: uppercase;
}