@import "../../_globalColor";

.education-card {
  display: flex;
  flex-direction: row;
  gap: 25px;
}

.education-card:hover + .education-card-border {
  border-color: $educationCardBorder;
  width: 100%;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.education-card-border {
  height: 30px;
  width: 80%;
  border-top: 2px solid $lightBorder1;
  margin-top: 20px;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.education-text-school {
  color: $textColor;
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  word-break: break-word;
}

.education-roundedimg {
  background-color: white;
  margin-right: 2rem;
  width: 6rem;
  height: 6rem;
  box-shadow: 0 0.5rem 1rem $lightBoxShadowDarker;
}

.education-roundedimg:hover{
  cursor: pointer;
  width: 7rem;
  height: 7rem;
}

.education-text-subHeader {
  padding-top: 0.8rem;
  color: $subTitle;
  font-weight: 700;
  font-size: 19px;
  margin: 0px;
}

.education-text-duration {
  color: $subTitle;
  font-size: 19px;
  margin: 0px;
  padding-top: 0.5rem;
}

/* Media Query */

@media (max-width: 1380px) {

  .education-card-border {
    width: 90%;
  }
  .education-text-subHeader {
    padding-top: 0.5rem;
    font-size: 16px;
  }
  .education-text-school {
    font-size: 20px;
  }
  .education-text-duration {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .education-card {
    padding-bottom: 10px;
    text-align: center;
    flex-direction: column;
  }
  .education-card-right {
    max-width: 100%;
  }
  .education-card-border {
    width: 100%;
  }
  .education-roundedimg {
    margin-top: 10px;
    width: 5rem;
    height: 5rem;
    margin-bottom: 1rem;
    margin-right: 0;
  }
  .education-roundedimg:hover{
    cursor: pointer;
    width: 6rem;
    height: 6rem;
  }
}
