@import "../../_globalColor";

.skills-main {
  display: flex;
}

.skills-text-div {
  margin-left: 50px;
}

.skills-main > * {
  flex: 1;
 //margin-bottom: 30px;
}

.skills-image-div > * {
  max-width: 100%;
  height: auto;
}

@media (max-width: 768px) {
  .skills-main {
    flex-direction: column;
  }
  .skills-text-div {
    margin: 20px;
  }
  .skills-text-subtitle {
    font-size: 16px;
    text-align: center;
  }
  .skills-image-div {
    order: 2;
  }
}
